@import url('https://fonts.googleapis.com/css?family=Roboto:100,200,300,400,400i,500,600,700');
html {
  -webkit-font-smoothing: antialiased!important;
  -moz-osx-font-smoothing: grayscale!important;
  -ms-font-smoothing: antialiased!important;
  overflow: auto;

  font-size: 62.5%;
  margin: 0;
  height: 100%;
}

body{
  --input-color: #99A3BA;
  --input-border: #CDD9ED;
  --input-background: #fff;
  --input-placeholder: #CBD1DC;
  --input-border-focus: #275EFE;
  --link-color:#4b8df8;
  --button-color:#551A8B;
  --accent-color:#b7c33b;
  --header-main-background:var(--accent-color);
  --header-main-link-color:#fff;
  --header-main-link-hover-color:#fff;
  --main-menu-background:#fff;
  --main-menu-link-color:#fff;
  --add-to-cart-background:var(--link-color);
  --add-to-favorite-background:var(--accent-color);
  --border-color: #e0e0e0;
  --pane-border-color:#f1f1f1;
  --font-color:#333;
  --header-version-name-border-color:#fff;

  --shadow-1: rgba(0, 0, 0, 0.1);
  --shadow-2: rgba(0, 0, 0, 0.2);
  --shadow-5: rgba(0, 0, 0, 0.5);
  --shadow-8: rgba(0, 0, 0, 0.8);
  --shadow-inset: rgba(255, 255, 255, 0.5);

  --wt-font-family-system: 'Roboto',system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,Oxygen,Ubuntu,Cantarell,"Droid Sans","Helvetica Neue",Arial,sans-serif;

  --ff-sans: var(--wt-font-family-system);
  --ff-serif: var(--wt-font-family-system);
  --ff-mono: var(--wt-font-family-system);
  --ff-bold: var(--wt-font-family-system);
  --theme-body-font-family: var(--wt-font-family-system);
  --border-radius: 4px;

  font-size:13px;
  color: var(--font-color);
  background: transparent;
  line-height: 1.20;
  margin: 0;
  padding: 0;

  font-weight: 400;
  font-family: var(--wt-font-family-system);

  -webkit-font-smoothing: antialiased;
}

@import "./_ie-fix.css";
@import "./bootstrap.css";
@import "./core.css";
@import "./spinners.css";
@import "./theme.css"; /** DONT REMOVE THIS! TEMPLATES USE THIS TO MANAGE THEME ON GENERATED PROJECTS*/

#dt-form tr{height:30px;max-height: 30px;padding: 0}
#dt-form td{height:30px;max-height: 30px;padding:0.2rem}

.table tbody tr:last-child{
  border: none;
}

.header-main{justify-content: space-between}

.modal-open .modal{
  background: rgba(255,255,255,0.7);
}

.modal-content{
  box-shadow: 0 12px 28px 0 var(--shadow-2),0 2px 4px 0 var(--shadow-1),inset 0 0 0 1px var(--shadow-inset);
  border-radius: 8px;
}

.header-main-logo img{
  width: 200px;
}

a{
  color: var(--link-color);
}